.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}
.loader img {
  width: 15%;
  margin-right: 1rem;
}
@media screen and (min-width: 64rem) {
  .loader img {
    width: 5%;
    margin-right: 1rem;
  }
}
.loader h1 {
  font-size: 2rem;
  font-weight: 500;
}
@media screen and (min-width: 64rem) {
  .loader h1 {
    font-size: 5rem;
    font-weight: 500;
  }
}